.issue-banner {
  @extend .z-depth-1;

  margin-bottom: 2em;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;

  * {
    position: relative;
    margin: 0;
    font-size: 24px;
    font-family: $font-libre;
    font-weight: 700;

    @include text-shadow;
  }

  a {
    text-decoration: none !important;
    color: $color-white !important;
    padding: 15px;
    min-height: 110px;
    display: flex;
    align-items: flex-end;

    span {
      display: block;
      width: 100%;
    }
  }

  &:hover, &:focus {
    @extend .z-depth-2;
  }

  &::before {
    content: '';

    @include cover;
  }

  &:nth-of-type(4n-3)::before {   // Every 4th, starting at the first
    background: rgba($color-orange, 0.5);
  }

  &:nth-of-type(4n-2)::before {  // Every 4th, starting at the second
    background: rgba($color-green, 0.5);
  }

  &:nth-of-type(4n-1)::before {  // Every 4th, starting at the second
    background: rgba($color-slate-dark, 0.5);
  }

  &:nth-of-type(4n)::before {  // Every 4th, starting at the second
    background: rgba($color-blue, 0.5);
  }
}



