* {
  box-sizing: border-box;
  font-family: $font-open;
}

.container {
  @media #{$medium-and-up} {
    width: 90%;
  }
  @media #{$large-and-up} {
    width: 85%;
  }
}

.flex {
  display: flex;
}

.flex-center {
  align-items: center;
}

.space-between {
  justify-content: space-between;
}

.space-evenly {
  justify-content: space-evenly;
}

strong {
  font-weight: 700;
}

img {
  max-width: 100%;
}

@mixin cover {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.background-paper {
  background-image: url('../images/paper-fibers.png');
  background-repeat: repeat;
  padding: 30px 0;
}



